import React from 'react'
import { graphql, StaticQuery } from 'gatsby'
import styled from 'styled-components'

import BackgroundImage from 'gatsby-background-image'

const BackgroundSection = ({ className }) => (
  <StaticQuery
    query={graphql`
      query {
        desktop: file(relativePath: { eq: "dan-smiling.png" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `}
    render={data => {
      // Set ImageData.
      const imageData = data.desktop.childImageSharp.fluid
      return (
        <div>
        <BackgroundImage
          Tag="section"
          className={className}
          fluid={imageData}
          id="hero-section"
          style={{ marginTop: `130px`, }}
        >
        </BackgroundImage>
          <div className="hero-textbox pad">
            <div id="award-hero-textbox">
              <div className="container">
                <div className="row">
                  <div className="col-sm-12 mb-5" style={{
                    maxWidth: `880px`,
                    marginLeft: `auto`,
                    marginRight: `auto`,
                  }}>
                    <h1 className="mb-5 semibold serif">Apply for an Award</h1>
                    <h2 className="h3 mb-3 serif">Eligibility</h2>
                    <p className="mb-5 small-copy">The journalistic content—text, audio, broadcast, video or graphics—must be published or aired to the public.</p>
                    <div className="row">
                      <div className="col-sm-12 col-md-6 pl-5 pr-5">
                        <ul className="award-list small-copy text-left">
                          <li>Open to United States news media in the form of print, broadcast, radio or digital. Freelance
                          journalists are eligible.
                        </li>
                          <li>Published or broadcast between Jan. 1, 2020, and Dec. 31, 2020. A series is eligible, but all content must be completed within the eligibility date range.</li>
                        </ul>
                      </div>
                      <div className="col-sm-12 col-md-6 pl-5 pr-5">
                        <ul className="award-list small-copy text-left">
                          <li>Collaborations are eligible for entry and count as one submission.</li>
                          <li>Journalists, a team of journalists, organizations or collaborations can submit up to two entries.</li>
                        </ul>
                      </div>
                    </div>
                    <hr />
                    <div className="row">
                      <div className="col-sm-12 col-md-6 borderright pl-5 pr-5 pt-4 pb-4">
                        <h2 className="h3 mb-4 serif">How to Submit</h2>
                        <ul className="award-list small-copy text-left">
                          <li>Applicants for the Dan Rather Medals submit their work directly online via the submission portal, beginning Jan. 2, 2021, through Feb. 1, 2021, by 5 p.m. Central.</li>
                          <li>Team entries must indicate a project team lead for contact.</li>
                        </ul>
                      </div>
                      <div className="col-sm-12 col-md-6 pl-5 pr-5 pt-4 pb-4">
                        <h2 className="h3 mb-4 serif">What to Submit</h2>
                        <ul className="award-list small-copy text-left">
                          <li>A single news story or up to three dated pieces in a series.</li>
                          <li>If the entry is a series, applicants may upload up to three installments/episodes for review by the judges, in chronological order.</li>
                          <li>A detailed statement (between 600 and 800 words) describing how the content meets the selection criteria of overcoming extraordinary conditions
in reporting.</li>
                          <li>Your statement and media materials are all submitted online through the Dan Rather Medals submission system. Media files must be uploaded directly to the submission portal
for consideration.</li>
                        </ul>
                      </div>
                    </div>
                    <hr className="mt-4" />
                    <div className="button-wrapper mt-5">
                      <p>Application will open Jan. 2, 2021</p>
                      {/* <a href="#" className="small-link button">Application will open Jan. 2, 2021</a> */}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )
    }}
  />
)

const ApplyStyledBackgroundSection = styled(BackgroundSection)`
  width: 100%;
  background-position: bottom center;
  background-repeat: repeat-y;
  background-size: cover;
`

export default ApplyStyledBackgroundSection
